<template>
  <v-card v-if="order.id">
    <!-- <v-toolbar> -->
    <v-card-title class="flex-grow-1">
      <span class="headline">Chi tit n hng {{ order.id }}</span>
    </v-card-title>
    <!-- </v-toolbar> -->
    <v-card-text>
      <v-row justify="space-between">
        <v-col cols="12" md="3">Trng thi: ang giao hng</v-col>
        <v-col cols="12" md="3">Nhn vin cht n: {{ fullName }}</v-col>
      </v-row>
      <v-simple-table>
        <thead>
          <tr>
            <th class="text-center" style="width:10%; font-size: 1rem;">STT</th>
            <th class="text-center" style="width:10%; font-size: 1rem;">ID</th>
            <th class="text-center" style="width:15%; font-size: 1rem;">
              Hnh nh
            </th>
            <th class="text-center" style="width:40%; font-size: 1rem;">
              Tn sn phm
            </th>
            <th class="text-center" style="width:10%; font-size: 1rem;">
              S lng
            </th>
            <th class="text-center" style="width:15%; font-size: 1rem;">
              n gi
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(product, index) in listProducts" :key="product.id">
            <td class="text-center">{{ index + 1 }}</td>
            <td class="text-center">{{ product.id }}</td>
            <td>
              <v-img
                contain
                :height="80"
                :width="80"
                class="mx-auto"
                :src="product.image"
              ></v-img>
            </td>
            <td class="text-center">{{ product.name }}</td>
            <td class="text-center">{{ product.quantity }}</td>
            <td class="text-center">{{ formatCurrency(product.price) }}</td>
          </tr>
        </tbody>
        <!-- <v-divider></v-divider> -->
        <tfoot>
          <!-- <td></td> -->
          <tr>
            <td colspan="5" class="text-end">
              <v-card-text class="mx-auto color:white">
                <label>
                  <b>Tng tin:</b>
                </label>
              </v-card-text>
            </td>
            <td class="text-center">
              <span style="color: red" data-tongtien
                >{{ order.total }}</span
              >
            </td>
          </tr>
        </tfoot>
      </v-simple-table>
      <v-row justify="space-between" >
        <v-col cols="12" md="3">
          <v-row class="pl-4"
            >Ngi mua:
            {{ order.user.first_name + " " + order.user.last_name }}</v-row
          >
          <v-row class="pl-4">Ngy mua: {{ order.createdAt }}</v-row>
          <v-row class="pl-4">S in thoi: {{ order.user.phone }}</v-row>
          <v-row class="pl-4">Phng thc thanh ton: Tin mt</v-row>
        </v-col>
        <v-col cols="12" md="3">
          <v-row class="pl-4">Ngi nhn: {{ order.name }}</v-row>
          <!-- <v-row class="pl-4">Ngy nhn d kin: 07/11/2019</v-row> -->
          <v-row class="pl-4">S in thoi: {{ order.phone }}</v-row>
          <v-row class="pl-4">a ch: {{ order.address }}</v-row>
          <v-row class="pl-4">Ghi ch: {{ order.note }}</v-row>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { formatCurrency } from "@/_api/format-currency";
import { orderService } from "@/_api";
import { userService } from "@/_api";

export default {
  data() {
    return {
      order: {},
      listProducts: [],
      userInfo: {}
    };
  },
  computed: {
    fullName() {
      return this.userInfo.firstName + " " + this.userInfo.lastName;
    }
  },
  methods: {
    formatCurrency(price) {
      return formatCurrency(price);
    },
    async getData() {
      this.userInfo = await userService.getProfile();
      const orderId = this.$route.params.orderId;
      this.order = await orderService.getOrder(orderId);
      this.listProducts = this.order.items.map(e => {
        return {
          id: e.product.id,
          quantity: e.quantity,
          name:
            "in thoi " +
            e.product.supplier +
            " " +
            e.product.name +
            " " +
            e.product.rom +
            " (" +
            e.product.color +
            ")",
          price: e.product.price,
          image: e.product.images[0].image
        };
      });
    }
  },
  created() {
    this.getData();
  }
};
</script>
